<!-- 蓝色简洁登录页面 -->
<template>
	<view class="login-bg">
		<image class="img-a" src="@/static/login/bg1.png"></image>
		<view class="t-login">
			<view class="t-b">登 录</view>
			<form class="cl">
				<view class="t-a">
					<image src="@/static/login/user.png"></image>
					<input  name="phone" placeholder="请输入手机号码" maxlength="11" v-model="phone" />
				</view>
				<view class="t-a">
					<image src="@/static/login/pwd.png"></image>
					<input type="password" name="code" maxlength="20" placeholder="请输入密码" v-model="pwd" />
				</view>
				<view style="display: flex;flex-direction: row;justify-content: space-between">
					<view class="t-c" style="margin-left: 20rpx;" @tap="LoginBySms()">短信登录</view>
					<view class="t-c" @tap="forgotPwd()">忘记密码</view>
				</view>

				<wButton
					text="登 录"
					:rotate="isRotate" 
					@click.native="login()"
					style="background-color: background: linear-gradient(to right, #ff8f77, #fe519f);;"
				></wButton>
			</form>
		</view>
		<view class="cardBox">
			<view>
				还没有登录账号？
				<text class="txt" @tap="reg()">立刻注册</text>
			</view>
			<view>
				嫌麻烦？
				<text class="txt" @tap="yijian()">本机号码一键登录（无法注册）</text>
			</view>
		</view>

		<image class="img-b" src="../../static/login/bg2.png"></image>
		
<!-- 		<wrap-version-update id="6302285884b51a00019ea12f" @check="handleCheck" @error="handleError"
		            @finish="handleFinish">
		</wrap-version-update> -->
	</view>
</template>
<script>
	let _this;
	import wButton from '../../components/watch-login/watch-button.vue' //button
export default {
	data() {
		return {
			phone: '', //手机号码
			pwd: '' ,//密码
			isRotate:false
		};
	},
	components:{
		wButton
	},
	mounted() {
		_this= this;
		this.yijian();
	},
	onLoad() {},
	methods: {
		yijian(){
			// #ifdef APP
			uni.login({
				provider:'univerify',
				univerifyStyle:{
					icon:{
						path:"static/logo.png"
					}
				},
				success: (res) => {
					uniCloud.callFunction({
						name:'yijian',
						data:res.authResult,
						success: (re) => {
							console.log(re);
							if(re.result.code==0){
								uni.showToast({
									icon:'success',
									title:'登录成功'
								});
								//保存token,user
								uni.setStorage({
									key:'token',
									data:re.result.token
								});
								uni.setStorage({
									key:'user',
									data:re.result.user
								});
								uni.setStorageSync('gender',re.result.gender);
								
								uni.switchTab({
									url:'../navigation/ShouYe/ShouYe'
								});
								uni.closeAuthView();
							}else{
								uni.closeAuthView();
								uni.showToast({
									title:re.result.msg,
									icon:'none',
									duration:5000
								});
							}
							
						}
					})
				}
			})
					
			// #endif

		},
		LoginBySms(){
			uni.navigateTo({
				url:'./loginBySms'
			})
		},
		//当前登录按钮操作
		login() {
			var that = this;
			if (!that.phone) {
				uni.showToast({ title: '请输入手机号', icon: 'none' });
				return;
			}
			if (!that.pwd) {
				uni.showToast({ title: '请输入密码', icon: 'none' });
				return;
			}
			uni.request({
				url:this.server_url+'/loginbyPassword?user='+this.phone+'&password='+this.pwd,
				method:'GET',
				success: (res) => {
					if(res.data.result=="登录成功"){
						//token,user保存
							uni.showToast({
								icon:'success',
								title:'登录成功'
							});
							//保存token,user
							uni.setStorage({
								key:'token',
								data:res.data.token
							});
							uni.setStorage({
								key:'user',
								data:this.phone
							});
							uni.setStorageSync('gender',res.data.gender);
							//判断是不是管理员
							if(this.phone=='admin'){
								uni.setStorage({
									key:'admin',
									data:true
								});
							}
							
							uni.switchTab({
								url:'../navigation/ShouYe/ShouYe'
							})
						}else{
							uni.showToast({
								icon:'error',
								position:'bottom',
								title:res.data.result
							});
							this.pwd="";
						}
				}
			});
		},
		//忘记密码
		forgotPwd() {
			uni.navigateTo({
				url:'./forget'
			})
		},
		//立刻注册
		reg() {
			uni.navigateTo({
				url:'./register'
			})
		},
		handleFinish(res){
			console.log(res)
		},
		handleError(res){
			console.log(res)
		},
		handleCheck(res){
			console.log(res)
		},
	}
};
</script>
<style>
.img-a {
	width: 100%;
}
.img-b {
	width: 100%;
	height: 45px;
	bottom: 0;
	position: absolute;
}
.login-bg {
	height: 100vh;
	background: linear-gradient(to bottom, #ff6a9a, #fe7d76);
}

.t-login {
	width: 580rpx;
	padding: 55rpx;
	margin: 0 auto;
	font-size: 28rpx;
	background-color: #ffffff;
	border-radius: 20rpx;
	position: relative;
	margin-top: -200rpx;
	box-shadow: 0 5px 7px 0 rgba(0, 0, 0, 0.15);
	z-index: 9;
}
.t-login button {
	font-size: 28rpx;
	background: linear-gradient(to right, #ff8f77, #fe519f);
	color: #fff;
	height: 90rpx;
	line-height: 90rpx;
	border-radius: 50rpx;
}

.t-login input {
	padding: 0 20rpx 0 120rpx;
	height: 90rpx;
	line-height: 90rpx;
	margin-bottom: 50rpx;
	background: #f6f6f6;
	border: 1px solid #f6f6f6;
	font-size: 28rpx;
	border-radius: 50rpx;
}

.t-login .t-a {
	position: relative;
}

.t-login .t-a image {
	width: 40rpx;
	height: 40rpx;
	position: absolute;
	left: 40rpx;
	top: 28rpx;
	padding-right: 20rpx;
}

.t-login .t-b {
	text-align: left;
	font-size: 46rpx;
	color: #ff939b;
	font-weight: bold;
	margin: 0 0 50rpx 20rpx;
}

.t-login .t-d {
	text-align: center;
	color: #999;
	margin: 80rpx 0;
}

.t-login .t-c {
	text-align: right;
	color: #c0c0c0;
	margin: -20rpx 30rpx 40rpx 0;
}

.t-login .t-f {
	text-align: center;
	margin: 200rpx 0 0 0;
	color: #666;
}

.t-login .t-f text {
	margin-left: 20rpx;
	color: #aaaaaa;
	font-size: 27rpx;
}

.t-login .uni-input-placeholder {
	color: #aeaeae;
}

.cl {
	zoom: 1;
}

.cl:after {
	clear: both;
	display: block;
	visibility: hidden;
	height: 0;
	content: '\20';
}
.cardBox1 {
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-box-align: center;
	-webkit-align-items: center;
	align-items: center;
	padding: 5px;
	background: #ffffff;
	opacity: 0.9;
	-webkit-border-radius: 20rpx;
	height: 70rpx;
	width: 600rpx;
	margin: 0 auto;
	position: relative;
	text-align: center;
	line-height: 70rpx;
	color: #aaa;
	font-size: 28rpx;
}
.cardBox {
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-box-align: center;
	-webkit-align-items: center;
	align-items: center;
	padding: 5px;
	background: #ffffff;
	opacity: 0.9;
	-webkit-border-radius: 20rpx;
	border-radius: 0 0 20rpx 20rpx;
	height: 150rpx;
	width: 600rpx;
	margin: 0 auto;
	position: relative;
	text-align: center;
	line-height: 70rpx;
	color: #aaa;
	font-size: 28rpx;
}
.cardBox .txt {
	color: #fe519f;
}
</style>
